.portfolio-hero {
    background: #00466E;
    background: linear-gradient(0deg, rgba(0, 70, 110, 1) 0%, rgba(1, 63, 99, 1) 30%, rgba(0, 29, 45, 1) 100%);
    position: relative; /* ensure pseudo-elements are positioned inside the section */
    overflow: hidden; /* prevent sub-pixel gaps from showing adjacent backgrounds */
}

.portfolio-hero::after {
    content: "";
    background: #00466E;
    background: linear-gradient(180deg, rgba(0, 70, 110, 0.05) 0%, rgba(0, 70, 110, 0.6) 30%, rgba(0, 70, 110, 0.95) 60%, rgba(0, 70, 110, 1) 100%);
    position: absolute;
    bottom: 0; /* align exactly with bottom to avoid fractional gaps */
    left: 0;
    right: 0;
    width: 100%;
    height: 200px;
    z-index: 0;
    pointer-events: none;
}

.portfolio-hero .portfolio-hero__heading {
    text-align: left;
    line-height: 1.5;
    font-size: 50px;
}

.portfolio-hero .portfolio-hero__heading span {
    color: #47DBFF;
}

.portfolio-hero .portfolio-hero__btn {
    width: fit-content;
    font-size: 20px;
    z-index: 1;
}

@media screen and (min-width: 0) and (max-width: 767px) {
    .portfolio-hero::after {
        content: "";
        background: #00466E;
        background: linear-gradient(180deg, rgba(0, 70, 110, 0.05) 0%, rgba(0, 70, 110, 0.6) 30%, rgba(0, 70, 110, 0.95) 60%, rgba(0, 70, 110, 1) 100%);
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 200px;
    }

    .portfolio-hero__image {
        width: 80% !important;
    }
}

@media screen and (min-width: 767.50px) and (max-width: 1200.50px) {
    .portfolio-hero__image {
        width: 70% !important;
    }
}

/*** Image Popup Effect ***/
/* --- Pop-Up Entrance Animation --- */
.portfolio-hero__image {
  opacity: 0;
  transform: scale(0.8) translateY(30px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.portfolio-hero__image.loaded {
  opacity: 1;
  transform: scale(1) translateY(0);
}

.portfolio-hero__image {
    width: 60%;
}
